*:focus {
  outline: none !important;
  box-shadow: 0 0 0 2px #2e2e2e;
}

.app {
  background: #303030;
  padding: 30px;
  height: 100vh;
}

.app * {
  border-radius: 4px;
}

.container {
  background: #e8e8e8;
  padding: 5px;
  max-width: 425px;
  margin-bottom: 5px;
}

.container-item {
  background: #fff;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.15);
  padding: 5px;
  font-size: 12px;
  border-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

input {
  width: 100%;
  box-sizing: border-box;
  border: 0;
  border-radius: 4px;
  box-shadow: 0;
  padding: 1rem;
  height: 2.25em;
}

.btn-dark {
  background-color: #2e2e2e;
  color: white;
  padding: 5px;
}

.grid-container {
  display: grid;
  grid-auto-rows: 1fr;
  grid-auto-columns: 1fr
}

a {
  font-size: 12px;
  color: #2e2e2e;
}

.mt {
  margin-top: 5px;
}

.mt-sm {
  margin-top: 3px;
}

.mb {
  margin-bottom: 5px;
}


footer {
  text-align: center;
  padding: 0px;
}